
import XHeader from 'vux/src/components/x-header/index.vue'

import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import serviceGetData from '../../service/getData.js'
import serviceUpData from '../../service/upData.js'

import { Cell, Checklist } from 'mint-ui';
console.log(1,Cell)

export default {
  components:{
    XHeader,Cell,
    XSwitch,
    Group,
    Toast
  },
  created(){
    serviceGetData.received().then(res=>{
      this.switch_val=res
      console.log(res)
    })

    serviceGetData.login_info().then(res=>{
      this.firstName=res.firstName // 当前登录的人名
      console.log(111, res)
    },res=>console.log(res))

  },
  methods:{
    to_about_fn(){
      this.$router.push({path:"/app/main/userInfo/about"})
    },
    switch_my(){
      serviceUpData.received({}).then(res=>{
        serviceGetData.received().then(res=>{
          this.switch_val=res
          if(res){
            // this.toast_text = '当前已打开通知'
          }else{
            // this.toast_text = '当前已关闭通知'
          }
          // this.toast_show = true
        })
      },()=>{
        serviceGetData.received().then(res=>{
          this.switch_val=res
        })
      })
    },
    onHide(){
      if(this.var_temp=='ok_submit'){
        this.$router.push({path:"/app/main/userInfo"})
      }
    },

  },
  data () {
    return {
      firstName:'', // 当前登录的人名
      switch_val: true,
      toast_show:false,
      toast_text:'',
      var_temp:'',
    }
  }
}



